.page-header {
  background-color: rgba(40, 54, 69, 1);
  color: white;
  margin-bottom: -67px;
  position: relative;
}

.page-header__flex {
  display: flex;
  flex-direction: column;
  width: auto;
  padding: 0;
  margin: 0 auto;
}

.page-header__container {
  background-color: rgba(40, 54, 69, 0.3);
  display: flex;
  align-items: center;
  min-height: 67px;
  border-bottom: solid 1px #000000;
}

.page-header__logo {
  display: block;
  margin: 0;
  margin-left: 20px;
  margin-right: auto;
  background-color: transparent;
}

.page-header__logo-image {
  display: block;
  width: 75px;
  height: 22px;
  --color-glasses: white;
  --color-glasses-star: @text-pink;
  --color-text: white;
  --color-text-star: @text-pink;

  &:hover {
    --color-glasses: @text-pink;
    --color-text: @text-pink;
  }

  &:active {
    --color-glasses: white;
    --color-glasses-star: @text-pink;
    --color-text: white;
    --color-text-star: @text-pink;
    opacity: 0.3;
  }
}

.page-header__toggle-nav {
  margin-right: 20px;
  background-color: transparent;
  border: none;
  padding: 0;
  position: relative;
}

.page-header--open-nav .page-header__toggle-nav {
  width: 25px;
  height: 25px;

  &::before,
  &::after {
    content: " ";
    position: absolute;
    top: 11px;
    right: 0;
    width: 26px;
    height: 3px;
    background-color: white;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:hover::before,
  &:hover::after {
    background-color: #d22856;
  }

  &:active::before,
  &:active::after {
    background-color: rgba(210, 40, 86, 0.3);
  }
}

.page-header--closed-nav {
  background-color: rgba(40, 54, 69, 0.7);
}

.page-header--closed-nav .page-header__container {
  border: none;
}

.page-header--closed-nav .page-header__nav {
  display: none;
}

.page-header--closed-nav .page-header__toggle-nav {
  width: 50px;
  height: 25px;

  &::before,
  &::after {
    content: " ";
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 5px;
    background-color: white;

    box-shadow: 0 10px 0 0 white, 0 20px 0 0 white;
  }

  &:hover::before,
  &:hover::after {
    background-color: #d22856;
    box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
  }

  &:active::before,
  &:active::after {
    background-color: rgba(210, 40, 86, 0.3);
    box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3),
      0 20px 0 0 rgba(210, 40, 86, 0.3);
  }
}

.page-header--no-js {
  background-color: rgba(40, 54, 69, 1);
}

.page-header--no-js .page-header__nav {
  display: block;
}

.page-header--no-js .page-header__toggle-nav {
  display: none;
}

.page-header--no-js .page-header__container {
  border-bottom: solid 1px #000000;
}

.page-header__logo-image--tablet {
  display: none;
}

.page-header__logo-image--desktop {
  display: none;
}

@media (min-width: @tablet-width) {
  .page-header {
    margin-bottom: -112px;
    min-height: 110px;
  }
  .page-header__flex {
    width: auto;
    padding: 0;
  }

  .page-header__logo {
    margin-left: 20px;
  }

  .page-header__logo-image {
    width: 147px;
    height: 40px;
  }

  .page-header__container {
    min-height: 110px;
  }

  .page-header__toggle-nav {
    margin-right: 20px;
  }

  .page-header__logo-image--tablet {
    display: block;
  }

  .page-header__logo-image--mobile {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .page-header {
    background-color: rgba(0, 0, 0, 0.3);
  }

  .page-header__flex {
    flex-direction: row;
    justify-content: space-between;
    width: 960px;
    padding: 0 120px;
  }

  .page-header__toggle-nav {
    display: none;
  }

  .page-header--closed-nav .page-header__nav {
    display: block;
  }

  .page-header__container {
    border-bottom: none;
    background-color: transparent;
  }

  .page-header__logo {
    margin-left: 0;
  }

  .page-header__logo-image--tablet {
    display: none;
  }

  .page-header__logo-image--desktop {
    display: block;
  }
}
