.page-header__toggle-nav {
  margin-right: 20px;
  background-color: transparent;
  border: none;
  padding: 0;
  position: relative;
}
.page-header--open-nav .page-header__toggle-nav {
  width: 25px;
  height: 25px;
}
.page-header--open-nav .page-header__toggle-nav::before,
.page-header--open-nav .page-header__toggle-nav::after {
  content: " ";
  position: absolute;
  top: 11px;
  right: 0;
  width: 26px;
  height: 3px;
  background-color: white;
}
.page-header--open-nav .page-header__toggle-nav::before {
  transform: rotate(45deg);
}
.page-header--open-nav .page-header__toggle-nav::after {
  transform: rotate(-45deg);
}
.page-header--open-nav .page-header__toggle-nav:hover::before,
.page-header--open-nav .page-header__toggle-nav:hover::after {
  background-color: #d22856;
}
.page-header--open-nav .page-header__toggle-nav:active::before,
.page-header--open-nav .page-header__toggle-nav:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.page-header--closed-nav {
  background-color: rgba(40, 54, 69, 0.7);
}
.page-header--closed-nav .page-header__container {
  border: none;
}
.page-header--closed-nav .page-header__nav {
  display: none;
}
.page-header--closed-nav .page-header__toggle-nav {
  width: 50px;
  height: 25px;
}
.page-header--closed-nav .page-header__toggle-nav::before,
.page-header--closed-nav .page-header__toggle-nav::after {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 5px;
  background-color: white;
  box-shadow: 0 10px 0 0 white, 0 20px 0 0 white;
}
.page-header--closed-nav .page-header__toggle-nav:hover::before,
.page-header--closed-nav .page-header__toggle-nav:hover::after {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.page-header--closed-nav .page-header__toggle-nav:active::before,
.page-header--closed-nav .page-header__toggle-nav:active::after {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
}
.page-header--no-js {
  background-color: #283645;
}
.page-header--no-js .page-header__nav {
  display: block;
}
.page-header--no-js .page-header__toggle-nav {
  display: none;
}
