  color: white;
  height: 94px;
  width: 276px;
  border: 2px solid #e5e5e5;
  border-bottom-color: #283645;
  border-top-color: #283645;
}
.prices__on-off {
  position: relative;
  background-color: white;
  border: 2px solid #e5e5e5;
}
.prices__on-off::before {
  position: absolute;
  right: 19px;
  top: 15px;
  content: " ";
  width: 28px;
  height: 14px;
  background-color: transparent;
  border: 3px solid #3dc644;
  border-top: none;
  border-right: none;
  transform: rotate(-45deg);
}
.prices__on-off--off::before,
.prices__on-off--off::after {
  content: " ";
  position: absolute;
  top: 30px;
  right: 20px;
  width: 35px;
  height: 3px;
  background-color: #f25b26;
  border: none;
}
.prices__on-off--off::before {
  transform: rotate(45deg);
}
.prices__on-off--off::after {
  transform: rotate(-45deg);
}
.prices__rate {
  display: block;
  margin: 0 auto;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
}
.prices__cost {
  display: block;
  margin: 0 auto;
  font-size: 24px;
  font-weight: lighter;
}
.prices__toggles {
  margin: 0 auto;
  margin-top: 45px;
}
.prices__toggles {
  display: flex;
  width: 62px;
  justify-content: space-between;
}
.prices__toggle {
  display: block;
  border: 3px solid transparent;
  background-clip: content-box;
  box-sizing: content-box;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  background-color: #bec2c7;
  padding: 0;
}
.prices__toggle--active {
  border: 3px solid #283645;
  background-color: transparent;
}
.prices__titel--hit {
  position: relative;
}
.prices__titel--hit::before {
  position: absolute;
  content: " ";
  left: -2px;
  top: -2px;
  background-image: url(../../img/bg-hit.svg);
  background-repeat: no-repeat;
  width: 74px;
  height: 73px;
}
.prices__titel--hit::after {
  position: absolute;
  content: "хит";
  font-size: 14px;
  text-transform: uppercase;
  transform: rotate(-45deg);
  left: 10px;
  top: 10px;
  font-weight: normal;
}
