.switch {
  border: none;
  background-color: #f2f2f2;
}
.switch__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  width: 280px;
  padding: 0 20px;
  margin: 0 auto;
}
.switch__item {
  margin: 0;
  padding: 0;
  margin-bottom: 51px;
}
.switch__title {
  position: relative;
  top: 57px;
  margin-bottom: 100px;
  color: #d22856;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  padding: 0;
  width: 220px;
}
.switch__label {
  font-size: 18px;
  line-height: 30px;
  position: relative;
  padding-left: 70px;
}
.switch__label::before {
  position: absolute;
  left: 0px;
  top: -4px;
  content: "";
  width: 35px;
  height: 35px;
  background-color: #ffffff;
  border: 2px solid #e5e5e5;
}
.switch__checkbox:hover + .switch__label::before {
  border-color: #283645;
}
.switch__checkbox:active + .switch__label::before {
  border-color: #d22856;
}
.switch__checkbox:focus + .switch__label::before {
  box-shadow: 0 0 0 1px #e59700;
}
.switch__checkbox:checked + .switch__label::before {
  background-image: url(../../img/icon-yes.svg);
  background-repeat: no-repeat;
  background-position: center center;
}
.switch--round .switch__item {
  margin-bottom: 45px;
}
.switch--round .switch__label::before {
  border-radius: 50%;
  top: -7px;
}
.switch--round .switch__checkbox:checked + .switch__label::after {
  position: absolute;
  left: 12px;
  top: 6px;
  content: "";
  width: 14px;
  height: 14px;
  background-color: #d22856;
  border-radius: 50%;
}
.switch--round .switch__checkbox:checked + .switch__label::before {
  background-image: none;
}
